import { Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import { AuthHttp } from 'angular2-jwt';
import { Site } from '../extra/gallery/site.class'

@Injectable()
export class OwnershipService {

  private fileUrl = 'http://localhost:5000/owned/gen';
  private ownershipUrl = 'http://localhost:5000/owned/';

  public genFile(): Promise<any> {
    return this.http.get(this.fileUrl).toPromise().then(r=> OwnershipService.downloadFile(r._body));
  }

  public static downloadFile(data: Response){
    var blob = new Blob([data], { type: 'text/plain' });
    var url= URL.createObjectURL(blob);
    var a         = document.createElement('a');
    a.href        = url;
    a.target      = '_blank';
    a.download    = data+'.txt';
    document.body.appendChild(a);
    a.click();
  }

  public fileCheck(id): Promise<any> {
    return this.http.get(this.ownershipUrl+id+'/file')
      .toPromise()
      .then(response => response.json() as String)
      .catch(OwnershipService.handleError);
  }


  public cnameCheck(id): Promise<any> {
    return this.http.get(this.ownershipUrl+id+'/cname')
      .toPromise()
      .then(response => response.json() as String)
      .catch(OwnershipService.handleError);
  }

  public metaCheck(id): Promise<any> {
    return this.http.get(this.ownershipUrl+id+'/meta')
      .toPromise()
      .then(response => response.json() as String)
      .catch(OwnershipService.handleError);
  }



  private static handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

  constructor(private http: AuthHttp) { }

}



